import React, { useState } from "react";

import { formatMinutes, hoursPerDay, parseTimeDelta } from "../utils/timeParsing";
import { useMember } from "../hooks/useMember";
import { useEstimate } from "../hooks/useEstimate";
import { WorkButton } from "../components/WorkButton";
import { CompleteCardButton } from "../components/CompleteCardButton";
import { useProvidedTrello } from "../hooks/useProvidedTrello";
import { OpenWorkCardButton } from "../components/OpenWorkCardButton";
import { closeIfOldAndEditCurrentWorkSession } from "../utils/workSessionsUtils";
import { WorkDetails } from "../components/WorkDetails";
import { useInit } from "../jk/hooks/useInit";

export function CardBackSectionPage() {
    const t = useProvidedTrello();
    const estimate = useEstimate(t);
    const [inputEstimate, setInputEstimate] = useState<string>("");
    const member = useMember(t, "id");

    useInit(() => {
        estimate.refresh();

        t.render(async () => {
            return estimate.refresh();
        });
    });

    function parseAndRun<T>(action: (parsedMinutes: number) => T) {
        const parsedMinutes = parseTimeDelta(inputEstimate);

        if (isNaN(parsedMinutes)) {
            t.alert({
                display: "error",
                message: "invalid input",
                duration: 4,
            });
            return;
        }

        const val = action(parsedMinutes);

        setInputEstimate("");

        return val;
    }

    function parseAndChangeEstimate(computeNewEstimate: (previousMinutes: number, parsedMinutes: number) => number) {
        parseAndRun(parsedMinutes => {
            estimate.set(current => ({
                ...current,
                estimateMinutes: computeNewEstimate(current.estimateMinutes, parsedMinutes),
            }));
        });
    }

    function parseAndChangeWork(computeNewWork: (previousMinutes: number, parsedMinutes: number) => number) {
        return parseAndRun(parsedMinutes =>
            estimate.set(current => ({
                ...current,
                workMinutes: {
                    ...current.workMinutes,
                    [member!.id]: computeNewWork(current.workMinutes[member!.id] ?? 0, parsedMinutes),
                },
            })));
    }

    function parseAndChangeWorkAndSession(args: {
        computeNewWork: (previousMinutes: number, parsedMinutes: number) => number,
        askConfirm: boolean,
        mouseEvent: React.MouseEvent<HTMLButtonElement, MouseEvent>,
    }) {
        runAndUpdateSession({
            action: () => parseAndChangeWork(args.computeNewWork),
            askConfirm: args.askConfirm,
            mouseEvent: args.mouseEvent,
        });
    }

    async function runAndUpdateSession(args: {
        action: () => ReturnType<typeof estimate.set>,
        askConfirm: boolean,
        mouseEvent: React.MouseEvent<HTMLButtonElement, MouseEvent>,
    }) {
        const delta = await args.action();

        if (!delta)
            return;

        const myWorkDelta = delta.workMinutes[member!.id];
        if (myWorkDelta === undefined)
            return;

        function updateSession() {
            return closeIfOldAndEditCurrentWorkSession(t, {
                action: async session => ({
                    ...session,
                    workMinutes: session.workMinutes + myWorkDelta,
                }),
                hoursPerDay,
                mouseEvent: args.mouseEvent,
            });
        }

        if (args.askConfirm) {
            t.popup({
                type: "confirm",
                title: "Add to session?",
                message: "Should this work change count in your work session?",
                confirmText: "Yes",
                onConfirm: tp => {
                    tp.closePopup();
                    return updateSession();
                },
                cancelText: "No",
                onCancel: tp => tp.closePopup(),
                mouseEvent: args.mouseEvent.nativeEvent,
            });
        }
        else {
            updateSession();
        }
    }

    return (
        <div>
            {estimate.value === undefined && (
                <p>Loading...</p>
            )}
            {estimate.value !== undefined && (<>
                <div className='card-input-header'>
                    <input
                        value={inputEstimate}
                        onChange={e => setInputEstimate(e.target.value)}
                        placeholder="input for all buttons..."
                    />
                    <WorkButton onEstimateChanged={estimate.refresh} />
                    <div className='top-right'>
                        <OpenWorkCardButton />
                        <CompleteCardButton />
                    </div>
                </div>
                <div>
                    <div className='card-label'>
                        Estimate: {formatMinutes(estimate.value.estimateMinutes)}
                    </div>
                    <button onClick={() => parseAndChangeEstimate((_, parsedMinutes) => parsedMinutes)}>
                        Set
                    </button>
                    <button onClick={() => parseAndChangeEstimate((previousMinutes, parsedMinutes) => previousMinutes + parsedMinutes)}>
                        +
                    </button>
                    <button onClick={() => parseAndChangeEstimate((previousMinutes, parsedMinutes) => previousMinutes - parsedMinutes)}>
                        -
                    </button>
                    <button
                        onClick={() => estimate.set(current => ({
                            ...current,
                            estimateMinutes: 0,
                        }))}
                    >
                        Clear
                    </button>
                </div>
                {member !== undefined && (
                    <div>
                        <WorkDetails
                            estimate={estimate.value}
                            member={member}
                        />
                        <button
                            onClick={e => parseAndChangeWorkAndSession({
                                computeNewWork: (_, parsedMinutes) => parsedMinutes,
                                askConfirm: true,
                                mouseEvent: e,
                            })}
                        >
                            Set
                        </button>
                        <button
                            onClick={e => parseAndChangeWorkAndSession({
                                computeNewWork: (previousMinutes, parsedMinutes) => previousMinutes + parsedMinutes,
                                askConfirm: false,
                                mouseEvent: e,
                            })}
                        >
                            +
                        </button>
                        <button
                            onClick={e => parseAndChangeWorkAndSession({
                                computeNewWork: (previousMinutes, parsedMinutes) => previousMinutes - parsedMinutes,
                                askConfirm: true,
                                mouseEvent: e,
                            })}
                        >
                            -
                        </button>
                        <button
                            onClick={e => runAndUpdateSession({
                                action: () => estimate.set(current => ({
                                    ...current,
                                    workMinutes: {
                                        ...current.workMinutes,
                                        [member!.id]: 0,
                                    },
                                })),
                                askConfirm: true,
                                mouseEvent: e,
                            })}
                        >
                            Clear mine
                        </button>
                    </div>
                )}
                {estimate.feedback && <div>{estimate.feedback}</div>}
            </>)}
        </div>
    );
}
