import { Trello } from "../types/trello";
import { fetchJson } from "./fetch";
import { enumerateKeys } from "./object";

const baseurl = "https://api.trello.com/1";

const apiKey = process.env.POWERUP_APP_KEY;

export function getAuthorizedRestApi(t: Trello.PowerUp.IFrame, scope: Trello.PowerUp.AllPermissions = "read,write") {
    return t.getRestApi()
        .isAuthorized()
        .then(authorized => {
            if (authorized)
                return;
            else
                throw new Error("unauthorized");
        })
        .catch(() => {
            return t.getRestApi().authorize({
                scope,
            });
        })
        .then(() => {
            return t.getRestApi().getToken();
        });
}

export function getCardFields(token: string, cardId: string, ...fields: ["all"] | (keyof Trello.PowerUp.Card)[]) {
    if (fields === undefined || fields.length === 0)
        fields = ["all"];

    return fetchJson<Trello.PowerUp.Card>({
        url: baseurl + `/cards/${cardId}?fields=${fields.join(",")}&key=${apiKey}&token=${token}`,
        method: "GET",
    });
}

export type EditableCard = Partial<Omit<Trello.PowerUp.Card, "id" | "idShort" | "url">>;

export function updateCardFields(token: string, cardId: string, fields: EditableCard) {
    const query = {
        ...fields,
        key: apiKey,
        token: token,
    };

    const queryString = Array.from(enumerateKeys(query), key => key + "=" + query[key]).join("&");

    return fetchJson<Trello.PowerUp.Card>({
        url: baseurl + `/cards/${cardId}?${queryString}`,
        method: "PUT",
    });
}
