export type Request = RequestInit & {
    url: RequestInfo | URL;
}

export type JsonResponse<T> = Response & {
    data: T;
    request: Request
}

export function fetchJson<T>(request: Request): Promise<JsonResponse<T>> {
    return fetch(request.url, {
        ...request,
        headers: {
            Accept: "application/json",
            ...request?.headers,
        },
    })
        .then(response => {
            return response.json().then(json => ({
                ...response,
                data: json as T,
                request: request,
            }));
        });
}
