import { Trello } from "../types/trello";

export function signRoutePath(t: Trello.PowerUp.IFrame, path: RoutePath, args?: { [key: string]: any}) {
    return t.signUrl(process.env.PUBLIC_URL + path, args);
}

export function signRoutePathAsAbsolute(t: Trello.PowerUp.IFrame, path: RoutePath, args?: { [key: string]: any}) {
    return t.signUrl(window.location.origin + process.env.PUBLIC_URL + path, args);
}
