import React from "react";
import { BrowserRouter, Routes } from "react-router-dom";
import { QueryClient, QueryClientProvider } from "react-query";

import { initializeCapabilities } from "./capabilities/capabilitiesInitilizer";
import { Dummy } from "./pages/DummyPage";
import { BoardSummaryPage } from "./pages/BoardSummaryPage";
import { AppRoute } from "./router/components/AppRoute";
import { IndexPage } from "./pages/IndexPage";
import { CardBackSectionPage } from "./pages/CardBackSection";

initializeCapabilities();

const queryClient = new QueryClient();

console.log("public url", process.env.PUBLIC_URL);

function App() {
    return (
        <QueryClientProvider client={queryClient}>
            <BrowserRouter basename={process.env.PUBLIC_URL}>
                <Routes>
                    <AppRoute
                        path="/"
                        element={<IndexPage />}
                    />
                    <AppRoute
                        path="/dummy"
                        element={<Dummy />}
                    />
                    <AppRoute
                        path="/board-page"
                        element={<BoardSummaryPage />}
                    />
                    <AppRoute
                        path="/card-back-section"
                        element={<CardBackSectionPage />}
                    />
                </Routes>
            </BrowserRouter>
        </QueryClientProvider>
    );
}

export default App;
