import React from "react";
import { useCard } from "../hooks/useCard";

import { useCurrentWork } from "../hooks/useCurrentWork";
import { useProvidedTrello } from "../hooks/useProvidedTrello";

type Props = {
    className?: string;
}

export function OpenWorkCardButton(props: Props) {
    const t = useProvidedTrello();
    const currentWork = useCurrentWork();
    const card = useCard(t, "id");

    if (currentWork.value?.cardId === undefined)
        return null;

    if (card === undefined || currentWork.value.cardId === card.id)
        return null;

    return (
        <button
            className={`open-work ${props.className}`}
            onClick={() => t.showCard(currentWork.value!.cardId)}
        >
            Open work card
        </button>
    );
}
