import { signRoutePathAsAbsolute } from "../router/signRoutePath";
import { Trello } from "../types/trello";

export function getCardBackSection(t: Trello.PowerUp.IFrame, props: CapabilityProps): Trello.PowerUp.CardBackSection {
    return {
        title: "Estimate",
        icon: props.baseUrl + props.icon.dark,
        content: {
            type: "iframe",
            url: signRoutePathAsAbsolute(t, "/card-back-section"),
            height: 200,
        },
    };
}
