import { useState, useMemo } from "react";

import { Estimate, getCardEstimate, setCardEstimate } from "../api/estimateApi";
import { Trello } from "../types/trello";
import { noDeps } from "../jk/noDeps";

export function useEstimate(t: Trello.PowerUp.IFrame, autosave = true) {
    const [estimate, setEstimate] = useState<Estimate>();
    const [feedback, setFeedback] = useState("");

    const refresh = useMemo(
        () => async () => {
            const fetchedEstimate = await getCardEstimate(t);
            setEstimate(fetchedEstimate);
        },
        noDeps,
    );

    const set = useMemo(
        () => (action: (estimate: Estimate) => Estimate) => {
            if (estimate === undefined)
                return;

            const updated = action(estimate);

            const delta: Estimate = {
                estimateMinutes: updated.estimateMinutes - estimate.estimateMinutes,
                workMinutes: {},
            };

            for (const memberId in updated.workMinutes)
                delta.workMinutes[memberId] = updated.workMinutes[memberId] - (estimate.workMinutes[memberId] ?? 0);

            if (!autosave)
                return;

            setFeedback("saving...");

            return getCardEstimate(t)
                .then(remoteEstimate => {
                    const updatedRemote: Estimate = {
                        estimateMinutes: remoteEstimate.estimateMinutes + delta.estimateMinutes,
                        workMinutes: {},
                    };

                    for (const memberId in delta.workMinutes)
                        updatedRemote.workMinutes[memberId] = (remoteEstimate.workMinutes[memberId] ?? 0) + delta.workMinutes[memberId];

                    return setCardEstimate(t, updatedRemote);
                })
                .then(updatedRemote => {
                    setEstimate(updatedRemote);
                    setFeedback("saved!");
                    setTimeout(() => setFeedback(""), 1000);
                    return delta;
                })
                .catch(err => {
                    setFeedback("");
                    t.alert({
                        display: "error",
                        message: `error during save: ${err}`,
                    });
                });
        },
        [estimate, autosave],
    );

    return useMemo(
        () => ({
            value: estimate,
            set,
            feedback,
            refresh,
        }),
        [estimate, feedback],
    );
}

export type UsedEstimate = ReturnType<typeof useEstimate>;
