import React, { useEffect, useState } from "react";

import { enumerateWork, Estimate, getRemainingWorkMinutes, getTotalWorkMinutes } from "../api/estimateApi";
import { formatMinutes } from "../utils/timeParsing";
import { useCard } from "../hooks/useCard";
import { useProvidedTrello } from "../hooks/useProvidedTrello";
import { Trello } from "../types/trello";
import { useCurrentWork } from "../hooks/useCurrentWork";
import { useElapsedMinutes } from "../hooks/useElapsed";

type Props = {
    estimate: Estimate,
    member: Trello.PowerUp.Member,
}

type MembersObject = {
    [key: Trello.PowerUp.Member["id"]]: Trello.PowerUp.Member,
}

export function WorkDetails(props: Props) {
    const t = useProvidedTrello();
    const { estimate, member } = props;
    const [showDetails, setShowDetails] = useState(false);
    const [members, setMembers] = useState<MembersObject>();
    const card = useCard(t, "id", "dueComplete");
    const currentWork = useCurrentWork();
    let elapsedWorkMinutes = useElapsedMinutes(currentWork.value?.startDate);

    if (currentWork.value?.cardId !== card?.id)
        elapsedWorkMinutes = 0;

    useEffect(
        () => {
            if (!showDetails || members !== undefined)
                return;

            t.board("members").then(res => {
                const obj: MembersObject = {};
                for (const m of res.members)
                    obj[m.id] = m;
                setMembers(obj);
            });
        },
        [showDetails, members],
    );

    const totalWork = getTotalWorkMinutes(estimate) + elapsedWorkMinutes;
    const myWork = elapsedWorkMinutes + estimate.workMinutes[member.id!] ?? 0;
    const remainingWork = getRemainingWorkMinutes(estimate, card?.dueComplete ?? false, totalWork);

    return (
        <>
            <div className='card-label'>
                Work: {formatMinutes(totalWork)}
                {" "}
                {myWork < totalWork && `(mine: ${formatMinutes(myWork)})`}
                {" "}
                {myWork < totalWork
                    && <span
                        className='work-details link-like'
                        onClick={() => setShowDetails(current => !current)}
                    >
                        details
                    </span>
                }
                {" | "} Remaining: {formatMinutes(remainingWork)}
            </div>
            {showDetails && members !== undefined
                && <div>
                    {[...enumerateWork(estimate)]
                        .filter(item => item.minutes > 0)
                        .map(item => {
                            const member = members[item.memberId];
                            return (
                                <div key={item.memberId}>
                                    {member?.fullName ?? "<missing>"}
                                    {member && " "}
                                    {member !== undefined ? `(${member.username})` : ""}
                                    {": "}
                                    {formatMinutes(item.minutes)}
                                </div>
                            );
                        })
                    }
                </div>
            }
        </>
    );
}
