import React from "react";

import { useCardWithRefresh } from "../hooks/useCard";
import { useProvidedTrello } from "../hooks/useProvidedTrello";
import { getAuthorizedRestApi, updateCardFields } from "../utils/trelloRest";

type Props = {
    className?: string;
}

export function CompleteCardButton(props: Props) {
    const t = useProvidedTrello();
    const { card, refresh } = useCardWithRefresh(t, "id", "due", "dueComplete");

    return (
        <button
            className={`complete-work ${props.className}`}
            onClick={() => {
                if (card === undefined)
                    return;

                getAuthorizedRestApi(t)
                    .then(token =>
                        updateCardFields(token, card.id, {
                            due: card.dueComplete
                                ? null
                                : card.due
                                    ? card.due
                                    : new Date().toISOString(),
                            dueComplete: !card.dueComplete,
                        }))
                    .catch(err => {
                        console.error(err);
                    })
                    // TODO: il refresh non funziona sempre, a volte la card rimane non aggiornata
                    .then(refresh);
            }}
        >
            {card === undefined
                ? "loading..."
                : card.dueComplete
                    ? "Uncomplete card"
                    : "Complete card"
            }
        </button>
    );
}
